import React from 'react';
import { UserDataContext } from '../_config/user-data-context';
import Chat from '../chat/chat';

import './toolbar.scss';

const ToolbarStatus = {
    UP_DOWNS: 1,
    AGENDA: 2,
    CHAT: 3,
}
const ACTIVE_CLASS = 'active';
const EMPTY_STRING = '';
const NAV_ENTRY_LIST = [
    { status: ToolbarStatus.UP_DOWNS, label: 'Ups & Downs'},
    { status: ToolbarStatus.AGENDA, label: 'Agenda'},
    { status: ToolbarStatus.CHAT, label: 'Chat'}
];

export default class Toolbar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            status: ToolbarStatus.UP_DOWNS,
            definedUserData: null,
            isLocalUser: false,
        }

        this.toggleData = this.toggleData.bind(this);
    }

    static contextType = UserDataContext;

    changeStatus(status) {
        this.setState({ status });
    }

    toggleData() {
        const { local, remote } = this.context;
        this.setState( (state) => {
            return {
                definedUserData: state.isLocalUser ? remote : local,
                isLocalUser: !state.isLocalUser
            };
        });
    }

    componentDidMount() {
        const { remote } = this.context;
        this.setState({
            definedUserData: remote,
        });
    } 

    render() {
        const { status, definedUserData } = this.state;

        const navEntryDomList = NAV_ENTRY_LIST.map((entry) => {
            return <li key={entry.status}
                className={ entry.status === this.props.status ? ACTIVE_CLASS : EMPTY_STRING }
                onClick={ () => { this.changeStatus(entry.status); } }>
                {entry.label}
            </li>;
        });

        const agendaEntryDomList = definedUserData && definedUserData.agenda.map((entry, index) => {
            return <p key={index}>{index + 1}. { entry }</p>;
        });
        const agendaDom = (<section className="agenda">
            { agendaEntryDomList }
        </section>);

        const upDownDom = (<section className="upDown">
            <h1>Highlight</h1>
            <p>{definedUserData && definedUserData.up}</p>
            <h1>Challenge</h1>
            <p>{definedUserData && definedUserData.down}</p>
        </section>);

        let toolbarClass= 'toolbar'
        if (status !== ToolbarStatus.CHAT) {
            toolbarClass += ' toolbar--switchable'
        }

        return (<aside data-testid="toolbar" className={toolbarClass}>
            <nav>
                <ol>{navEntryDomList}</ol>
            </nav>
            { status === ToolbarStatus.CHAT && <Chat></Chat> }
            { status === ToolbarStatus.AGENDA && agendaDom }
            { status === ToolbarStatus.AGENDA && <button className="button--icon" onClick={ this.toggleData } title="Switch User">Switch User</button> }
            { status === ToolbarStatus.UP_DOWNS && upDownDom }
            { status === ToolbarStatus.UP_DOWNS && <button className="button--icon" onClick={ this.toggleData } title="Switch User">Switch User</button> }
        </aside>);
    }
};