import React from 'react';
import { UserDataContext } from '../_config/user-data-context';
import './chat.scss';

export default class Local extends React.Component {

    static contextType = UserDataContext;

    render() {

        const { messageList, messageInput } = this.context.chat;

        return (<section className="chat" data-testid="chat">
            <div className="chat__history">{messageList}</div>
            <div className="chat__input">{messageInput}</div>
        </section>);
    }
};