import * as SWRTC from "@andyet/simplewebrtc";
import React, { Component } from 'react';
import { Provider } from "react-redux";
import './App.css';
import ChatMessageGroup from './mento/chat-message-group/chat-message-group';
import Client from './mento/client/client';
import { UserDataContext } from './mento/_config/user-data-context';

class App extends Component {
    render() {

        const { mentoClient } = window;

        // ====================================================================
        // IMPORTANT SETUP
        // ====================================================================
        // Replace `YOUR_API_KEY` here with the API key you received when
        // signing up for SimpleWebRTC
        // --------------------------------------------------------------------
        const API_KEY = 'YOURKEY';
        // ====================================================================

        const ROOM_NAME = mentoClient && mentoClient.room && mentoClient.room.name;
        const ROOM_PASSWORD = mentoClient && mentoClient.room && mentoClient.room.password;
        const CONFIG_URL = `https://api.simplewebrtc.com/config/guest/${API_KEY}`;

        if (!ROOM_NAME) {
            return (
                <h1>No Session</h1>
            );
        }

        const store = SWRTC.createStore();

        return (
            <Provider store={store}>
                <SWRTC.Provider configUrl={CONFIG_URL} >
                    {/* Render based on the connection state */}
                    <SWRTC.Connecting>
                        <h1>Connecting...</h1>
                    </SWRTC.Connecting>

                    <SWRTC.Disconnected>
                        <h1>Lost connection. Reattmpting to join...</h1>
                    </SWRTC.Disconnected>

                    <SWRTC.Connected>
                        {/* Request the user's media */}
                        <SWRTC.RequestUserMedia audio video auto />
                        {/* Enable playing remote audio. */}
                        <SWRTC.RemoteAudioPlayer />

                        {/* Connect to a room with a name and optional password */}
                        <SWRTC.Room name={ROOM_NAME} password={ROOM_PASSWORD}>
                            {({ room, localMedia, remoteMedia }) => (
                                <SWRTC.UserControls render={({ isDeafened, deafen, undeafen, isMuted, mute, unmute, isPaused, pauseVideo, resumeVideo, user }) => {

                                    user.displayName = mentoClient.username || user.displayName;
                                    if (!room.joined) {
                                        return <h1>Joining room...</h1>;
                                    }

                                    const remoteVideos = remoteMedia.filter(m => m.kind === 'video');
                                    const localVideos = localMedia.filter(m => m.kind === 'video' && m.shared);

                                    const userContext = {
                                        ...mentoClient,
                                        local: {
                                            ...mentoClient.local,
                                            video: (<SWRTC.Video media={localVideos[0]} />),
                                            controls: {
                                                isMuted,
                                                unmute,
                                                mute,
                                                isPaused,
                                                resumeVideo,
                                                pauseVideo,
                                            }
                                        },
                                        remote: {
                                            ...mentoClient.remote,
                                            video: (<SWRTC.Video media={remoteVideos[0]} />),
                                            controls: {
                                                isDeafened,
                                                undeafen,
                                                deafen,
                                            }
                                        },
                                        chat: {
                                            messageList: (<SWRTC.ChatList room={room.address} renderGroup={({ chats, peer }) => {
                                                const groupPropertyList = {
                                                    id: chats[0].id,
                                                    displayName: peer.displayName,
                                                    timestamp: chats[0].time.toLocaleTimeString(),
                                                    chats,
                                                };
                                                return (<ChatMessageGroup {...groupPropertyList} key={chats[0].id}></ChatMessageGroup>);
                                            }}></SWRTC.ChatList>),
                                            messageInput: (<SWRTC.ChatInput
                                                room={room.address}
                                                placeholder='Send a message...'
                                            />),
                                        }
                                    }
                                    return (
                                        <UserDataContext.Provider value={userContext}>
                                            <Client></Client>
                                        </UserDataContext.Provider>
                                    );
                                }}/>
                            )}
                        </SWRTC.Room>
                    </SWRTC.Connected>
                </SWRTC.Provider>
            </Provider>
        );
    }
}

export default App;
