import React from 'react';
import Local from '../local/local';
import Timer from '../timer/timer';
import Toolbar from '../toolbar/toolbar';
import { UserDataContext } from '../_config/user-data-context';
import './client.scss';

export default class Client extends React.Component {

    static contextType = UserDataContext;

    render() {

        const { video, controls } = this.context.remote;
        const { isDeafened, deafen, undeafen } = controls;

        const deafenButtonClass = isDeafened ? 'button--icon remote__mute remote__mute--undeafen' : 'button--icon remote__mute remote__mute--deafen';
        const deafenButtonTitle = isDeafened ? 'Undeafen' : 'Deafen';

        return (<div className="client" data-testid="client">
            <main>
                <Local></Local>
                <div className="remote__video">{video}</div>
                <button className={deafenButtonClass}
                        title={deafenButtonTitle}
                        onClick={() => isDeafened ? undeafen() : deafen()}>
                    {deafenButtonTitle}
                </button>
                <Timer className="timer"></Timer>
            </main>
            <Toolbar></Toolbar>
        </div>);
    }
};