import React from 'react';
import './local.scss';
import { UserDataContext } from '../_config/user-data-context';

export default class Local extends React.Component {

    static contextType = UserDataContext;

    render() {
        const { video, controls } = this.context.local;
        const { isMuted, mute, unmute, isPaused, pauseVideo, resumeVideo } = controls;

        const visibiltyTitle = isPaused ? 'Resume' : 'Pause';
        const visibiltyClass = isPaused ? 'button--icon local__visibility local__visibility--resume' : 'button--icon local__visibility local__visibility--pause';
        const muteTitle = isMuted ? 'Unmute' : 'Mute';
        const muteClass = isMuted ? 'button--icon local__mute local__mute--unmute' : 'button--icon local__mute local__mute--mute';

        return (<section className="local" data-testid="local">
            <button className={visibiltyClass}
                    title={visibiltyTitle}
                    onClick={() => isPaused ? resumeVideo() : pauseVideo()}>
                    {visibiltyTitle}
            </button>
            <button className={muteClass}
                    title={muteTitle}
                    onClick={() => isMuted ? unmute() : mute()}>
                    {muteTitle}
            </button>
            <div className="local__video">{video}</div>
        </section>);
    }
};