import React from 'react';
import { SESSION_START, SESSION_TIMER_CONFIG } from '../_config/timer';
import './timer.scss';

export default class Timer extends React.Component {
    state = {
        sessionState: '',
        seconds: '00',   // responsible for the seconds 
        minutes: '00'  // responsible for the minutes
    };
    secondsRemaining;
    intervalHandle;

    componentDidMount() {
        const sessionConfig = SESSION_TIMER_CONFIG[SESSION_START];
        this.secondsRemaining = sessionConfig.seconds;
        var min = Math.floor(this.secondsRemaining / 60);
        if (min < 10) {
            min = '0' + String(min);
        }
        this.setState({
            minutes: min,
            sessionState: SESSION_START,
        })
        this.startCountDown();
    }
    componentWillUnmount() {
        clearInterval(this.intervalHandle);
    }

    tick() {
        this.secondsRemaining--;

        if (this.secondsRemaining === 0) {
            const sessionConfig = SESSION_TIMER_CONFIG[this.state.sessionState];
            this.setState({
                sessionState: sessionConfig.next,
            });
            const nextDessionConfig = SESSION_TIMER_CONFIG[sessionConfig.next];
            if (nextDessionConfig) {
                this.secondsRemaining = nextDessionConfig.seconds;
            }
        }

        var min = Math.floor(this.secondsRemaining / 60);
        var sec = this.secondsRemaining - (min * 60);
        this.setState({
            minutes: min,
            seconds: sec,
        });
        if (sec < 10) {
            this.setState({
                seconds: "0" + this.state.seconds,
            });
        } 
        if (min < 10) {
            this.setState({
                minutes: "0" + min,
            });
        }
    }

    startCountDown() {
        this.intervalHandle = setInterval(() => {
            this.tick();
            if (this.secondsRemaining === 0) {
                clearInterval(this.intervalHandle);
            }
        }, 1000);
    }

    render() {
        const { minutes, seconds, sessionState } = this.state;

        return (<section data-testid="timer" className="timer">
            <p className="timer__legend">Session {sessionState}</p>
            <p className="timer__segment">{minutes}</p>
            <p className="timer__devider">:</p>
            <p className="timer__segment">{seconds}</p>
        </section>);
    }
}
