import React from 'react';
import './chat-message-group.scss';

const CHAT_LIST_CLASS='chat__history';

export default class Local extends React.Component {

    shouldScroll() {
        if (!this.lastEntry) {
            return false;
        }
        if (!this.findParentChatList(this.lastEntry)) {
            return false;
        }
        if (this.oldElement && this.oldElement === this.lastEntry) {
            return false;
        }
        return true;
    }

    findParentChatList(element) {
        if (element.classList.contains(CHAT_LIST_CLASS)) {
            return element;
        }
        return this.findParentChatList(element.parentNode);
    }

    componentDidUpdate() {
        if (this.shouldScroll()) {
            this.oldElement = this.lastEntry;
            const parent = this.findParentChatList(this.lastEntry);
            parent.scrollTop = this.lastEntry.offsetTop - parent.offsetTop;
        }
    }

    render() { 
        const {id, displayName, timestamp, chats} = this.props;
        return (<div className="chat-message-group" data-testid="chat-message-group" key={id}>
            <div className="chat-message-group__meta">
                <div className="chat-message-group__meta__name">{displayName ? displayName : 'Anonymous'}</div>
                <div className="chat-message-group__meta__timestamp">{timestamp}</div>
            </div>
            {chats && chats.map((message) => (
                <p className="chat-message chat-message-group__entry"
                    key={message.id}
                    ref={el => (this.lastEntry = el)}>
                    {message.body}
                </p>
            ))}
        </div>);
    }
};